
import * as React from 'react';
import Button from '@mui/material/Button';
import Reac, { useState, useEffect, useRef } from 'react';


export default function SettingsPage({terminal,ledsettings}) {


    const [amount,setAmount] = useState(0);


    const callbacker = (val)=>
    {
        setAmount(val);


    }
    return (
         <div>
            <div>Settings Page</div>
            <Button onClick={()=>terminal.sendUpdate("./build/ddttr_copy_20230816133501.ino.bin",callbacker) }>Send</Button>
            <div>{amount}</div>
        </div>
        );

}